<template>
  <div class="container">
    <div id="recaptcha-container"></div>       
    <form class="card-panel" @submit.prevent="verifyOtp">        
      <h4 class="center deep-purple-text">Ajoah  2</h4>
      <div style="height:30px"></div>      
      <div class="row">
          <div class="field col s12">              
              <label for="nickname">()</label>
              <input id="nickname" type="text" class="validate" data-length="30" v-model="nickname">              
              <!-- <span class="helper-text" data-error="wrong" data-success="right"></span> --> 
          </div>
      </div> 
      <div class="row">        
        <div class="field col s12">         
          <label for="phNo"></label>
          <input id="phNo" type="number" class="validate" data-length="12" v-model="phNo" @keyup="getPhoneChk(phNo)">
          <!-- <input id="icon_prefix" type="number" class="validate" data-length="12" v-model="phNo">            -->
          <!-- <span class="helper-text" data-error="wrong" data-success="right"></span>-->
          <span id='#help' class="red-text text-accent-4">{{this.gaipPos}}</span>                                       
        </div>             
        <div class="field right">
            <a class="waves-effect waves-teal btn-flat" @click="sendOtp"></a>
        </div>          
      </div>      
      <div class="row">        
        <div class="field col s12">         
          <label for="otp"></label>
          <input id="otp" type="number" v-model="otp" value="">           
        </div>      
        <!-- <div class="field right col s12">            
            <a class="waves-effect waves-teal btn-flat" @click="sendOtp"></a>                         
            <a class="waves-effect waves-teal btn-flat" @click="verifyOtp"></a>               
        </div>         --> 
      </div>      
       <div class="field center">
        <button class="btn deep-purple"> </button>
      </div>      
    </form>        
  </div>  
</template>

<script>
import db from '@/firebase/init'
import moment from 'moment'
import slugify from 'slugify'
import * as firebase from "firebase/app"
import "firebase/auth";
import "firebase/firestore";

export default {
  name: 'Signup',
  data(){
    return{
      email: null,
      password: null,
      alias: null,
      feedback: null,
      slug: null,
      phNo: null,
      appVerifier : '',
      otp : '',
      answer : '',
      nickname : '',
      vefication_code : false,
      gaipPos :  ''
    }
  },
  created(){
    this.$store.commit('select_view_false')       
    // console.log("SetPassword this.$store.state.select_view" + this.$store.state.select_view)
  },
  methods: {
     getPhoneChk(val) {
        let res = this.getGaipYubu(val)
        this.gaipPos = res
        //   '-'    
        // this.model.phNo = this.phNo.replace(/[^0-9]/g, '')
    },
    getGaipYubu( phoneNumber ) {
      if(!phoneNumber) return phoneNumber
      phoneNumber = phoneNumber.replace(/[^0-9]/g, '')
  
      let res = ''
      if(phoneNumber.length < 3) {
          
      }
      else {
          if(phoneNumber.substr(0, 2) =='02') {
      
              if(phoneNumber.length <= 5) {//02-123-5678
                  
              }
              else if(phoneNumber.length > 5 && phoneNumber.length <= 9) {//02-123-5678
                  
              }
              else if(phoneNumber.length > 9) {//02-1234-5678
                  
              }
      
          } else {
              
              if(phoneNumber.length > 9) { //010-1234-5678
                  this.chksignup()                                
              }
          }
      }
      return res
      },
    chksignup(){
         
         let vm = this
         let countryCode = '+82' //
        //  let countryCode = '+1'
         let phoneNumber = countryCode + this.phNo.substring(1)
         let ref = db.collection('users').doc(phoneNumber)
         
         ref.get().then(doc => {

           if(doc.exists){
              // alert(this.phNo + '    .');
              this.gaipPos = "   ."
              return;
           }else{                          
              // alert(this.phNo + '   .');              
              this.gaipPos = " ."
           }
         })                                            
    },
    signup(){
         
         let vm = this
         let countryCode = '+82' //
        //  let countryCode = '+1'
         let phoneNumber = countryCode + this.phNo.substring(1)
         let ref = db.collection('users').doc(phoneNumber)
         
         ref.get().then(doc => {

           if(doc.exists){
              alert(this.phNo + '    .');
           }else{            
              // Add a new document in collection "cities"
              ref.set({
                nickname: this.nickname,
                reg_datetime : Date.now(),
                sms_phone_number : this.phNo,
                sms_auth_number : this.otp,
                sms_auth_yn : 'Y',
                noti_req_channel : 'MobileWeb',
                sms_auth_datetime : Date.now() 
              }) 
              .then(function() {
                  console.log("Document successfully written!");
                  M.toast({html: '  .   .', classes: 'rounded'})
                  
                  //    
                  vm.$router.push({path:'/setPassword'})
              })
              .catch(function(error) {
                  console.error("Error writing document: ", error);
              });  

           }
         })                                            
    },    
    sendOtp(){

        let vm = this
         let countryCode = '+82' //
        //  let countryCode = '+1'
         let phoneNumber = countryCode + this.phNo.substring(1)
         let ref = db.collection('users').doc(phoneNumber)
         
         ref.get().then(doc => {

           if(doc.exists){              
              M.toast({html: '   .<br/> .', classes: 'rounded'})                 
              let vm = this
              //  
              vm.$router.push({path:'/login'}) 
              
           }else{                          
              if(this.phNo.length == 0){
                alert('  !');
              }else if(this.phNo.length < 10){
                alert('   . ['+this.phNo.length+']');
              }else{
                
                //let countryCode = '+1'
                let countryCode = '+82' //          
                let phoneNumber = countryCode + this.phNo
                
                // let countryCode = '+1' //          
                // let phoneNumber = countryCode + '6505551234'

                let appVerifier = this.appVerifier                    
                
                firebase.auth().signInWithPhoneNumber(phoneNumber, appVerifier)
                  .then(function (confirmationResult) {
                    // SMS sent. Prompt user to type the code from the message, then sign the
                    // user in with confirmationResult.confirm(code).
                    window.confirmationResult = confirmationResult;
                    console.log("window.confirmationResult" + window.confirmationResult)      
                    console.log("confirmationResult" + confirmationResult)       
                    this.vefication_code = true;
                  }).catch(function (error) {
                  // Error; SMS not sent
                  // ...                        
                });
              }
           }
         })                        
      },
      //
      verifyOtp(){
        if(this.vefication_code){
        // if(!this.vefication_code){
          alert('    .');
        }else if(this.phNo.length < 10){
          alert('   .');
        }else if(this.otp.length != 6){
          alert(' 6  .');
        }else{
                    
          let vm = this
          let code = this.otp

          //         ...  .. 
          this.signup(); //  
          console.log("code :" + code)                             
          window.confirmationResult.confirm(code).then(function (result) {
            // User signed in successfully.
            var user = result.user;            
            //route to set password !            
            this.signup(); //  
             
            console.log("Success");
                          
          }).catch(function (error) {
            // User couldn't sign in (bad verification code?)
            // ...
            console.log("Fail");
  
          });
        }
        //
      },
      initReCaptcha(){
        setTimeout(()=>{
          let vm = this
          window.recaptchaVerifier = new firebase.auth.RecaptchaVerifier('recaptcha-container', {
            'size': 'invisible',
            'callback': function(response) {
              // reCAPTCHA solved, allow signInWithPhoneNumber.
              // ...
            },
            'expired-callback': function() {
              // Response expired. Ask user to solve reCAPTCHA again.
              // ...
            }
          });
          //
          this.appVerifier =  window.recaptchaVerifier
        },1000)
      }
  },
    created(){
      this.initReCaptcha()
    }
} 
</script>

<style>
.signup{
  max-width: 400px;
  margin-top: 60px;
}
.signup h2{
  font-size: 2.4em;
}
.signup .field{
  margin-bottom: 16px;
}
</style>




