<template>
  <div class="container">
    <div id="recaptcha-container"></div>       
    <form class="card-panel" @submit.prevent="changeProfile">        
      <h3 class="center deep-purple-text">() </h3>
      <div style="height:30px"></div>      
      <div class="row">
          <div class="field col s12">              
              <label for="icon_prefix">()</label>
              <input id="icon_prefix" type="text" class="validate" data-length="20" v-model="nickname">              
              <!-- <span class="helper-text" data-error="wrong" data-success="right"></span> -->
          </div>
      </div>       
       <div class="field center">
        <button class="btn deep-purple"></button>
      </div>      
    </form>        
  </div>  
</template>

<script>
import db from '@/firebase/init'
import moment from 'moment'
import slugify from 'slugify'
import * as firebase from "firebase/app"
import "firebase/auth";
import "firebase/firestore";

export default {
  name: 'setProfile',
  data(){
    return{
      email: null,
      password: null,
      alias: null,
      feedback: null,
      slug: null,
      phNo: null,
      appVerifier : '',
      otp : '',
      answer : '',
      nickname : '',
      vefication_code : false,
      gaipPos :  ''
      
    }
  },
  created(){
    this.$store.commit('select_view_false')       
    // console.log("SetPassword this.$store.state.select_view" + this.$store.state.select_view)
    let user = firebase.auth().currentUser
      firebase.auth().onAuthStateChanged((user) => {
        if(user){
          this.user = user        
        } else {
          this.user = null
          
          let vm = this
    
          vm.$router.push({path:'/login'})
          window.location.reload();
        }  
      })
  },
  methods: {
     getPhoneChk(val) {
        let res = this.getGaipYubu(val)
        this.gaipPos = res
        //   '-'    
        // this.model.phNo = this.phNo.replace(/[^0-9]/g, '')
    },
    getGaipYubu( phoneNumber ) {
      if(!phoneNumber) return phoneNumber
      phoneNumber = phoneNumber.replace(/[^0-9]/g, '')
  
      let res = ''
      if(phoneNumber.length < 3) {
          
      }
      else {
          if(phoneNumber.substr(0, 2) =='02') {
      
              if(phoneNumber.length <= 5) {//02-123-5678
                  
              }
              else if(phoneNumber.length > 5 && phoneNumber.length <= 9) {//02-123-5678
                  
              }
              else if(phoneNumber.length > 9) {//02-1234-5678
                  
              }
      
          } else {
              
              if(phoneNumber.length > 9) { //010-1234-5678
                  this.chksignup()                                
              }
          }
      }
      return res
      },
    chksignup(){
         
         let vm = this
         let countryCode = '+82' //
        //  let countryCode = '+1'
         let phoneNumber = countryCode + this.phNo.substring(1)
         let ref = db.collection('users').doc(phoneNumber)
         
         ref.get().then(doc => {

           if(doc.exists){
              // alert(this.phNo + '    .');
              this.gaipPos = "   ."
              return;
           }else{                          
              // alert(this.phNo + '   .');              
              this.gaipPos = " ."
           }
         })                                            
    },
    changeProfile(){
         
         let vm = this
         var user = firebase.auth().currentUser;          
         let phoneNumber = firebase.auth().currentUser.phoneNumber;

         console.log("phoneNumber = " + phoneNumber);
         let ref = db.collection('users').doc(phoneNumber).update({
            "nickname": this.nickname,            
          })
        .then(function() {
            console.log("Document successfully updated!");
            // M.toast({html: '() .', classes: 'rounded'})                 
            // alert('() .  .') 
            M.toast({html: '() .', classes: 'rounded'})
            // vm.$router.push({path:'/index'})
            // window.location.reload();  
            firebase.auth().signOut().then(() => {
                this.$router.push({ name: 'Login' })
                window.location.reload();
            })
        });                                         
    },    
    sendOtp(){

        let vm = this
         let countryCode = '+82' //
        //  let countryCode = '+1'
         let phoneNumber = countryCode + this.phNo.substring(1)
         let ref = db.collection('users').doc(phoneNumber)
         
         ref.get().then(doc => {

           if(doc.exists){              
              M.toast({html: '   .<br/> .', classes: 'rounded'})                 
              let vm = this
              //  
              vm.$router.push({path:'/login'}) 
              
           }else{                          
              if(this.phNo.length == 0){
                alert('  !');
              }else if(this.phNo.length < 10){
                alert('   . ['+this.phNo.length+']');
              }else{
                
                //let countryCode = '+1'
                let countryCode = '+82' //          
                let phoneNumber = countryCode + this.phNo
                //
                let appVerifier = this.appVerifier                    
                
                firebase.auth().signInWithPhoneNumber(phoneNumber, appVerifier)
                  .then(function (confirmationResult) {
                    // SMS sent. Prompt user to type the code from the message, then sign the
                    // user in with confirmationResult.confirm(code).
                    window.confirmationResult = confirmationResult;               
                    this.vefication_code = true;
                  }).catch(function (error) {
                  // Error; SMS not sent
                  // ...                        
                });
              }
           }
         })                        
      },
      //
      verifyOtp(){
        if(this.vefication_code){
        // if(!this.vefication_code){
          alert('    .');
        }else if(this.phNo.length < 10){
          alert('   .');
        }else if(this.otp.length != 6){
          alert(' 6  .');
        }else if(this.answer != ''){          
          alert('   3    .\n( : *, *, *)');
        }else{
                    
          let vm = this
          let code = this.otp
          
          this.signup(); //  

          // 
          window.confirmationResult.confirm(code).then(function (result) {
            // User signed in successfully.
            var user = result.user;            
            //route to set password !
            
            this.signup(); //  
            console.log("Success");
          }).catch(function (error) {
            // User couldn't sign in (bad verification code?)
            // ...
            console.log("Fail");
          });
        }
        //
      },
      initReCaptcha(){
        setTimeout(()=>{
          let vm = this
          window.recaptchaVerifier = new firebase.auth.RecaptchaVerifier('recaptcha-container', {
            'size': 'invisible',
            'callback': function(response) {
              // reCAPTCHA solved, allow signInWithPhoneNumber.
              // ...
            },
            'expired-callback': function() {
              // Response expired. Ask user to solve reCAPTCHA again.
              // ...
            }
          });
          //
          this.appVerifier =  window.recaptchaVerifier
        },1000)
      }
  },
    created(){
      this.initReCaptcha()
    }
} 
</script>

<style>
.signup{
  max-width: 400px;
  margin-top: 60px;
}
.signup h2{
  font-size: 2.4em;
}
.signup .field{
  margin-bottom: 16px;
}
</style>




